import React, { FC } from 'react';
import styled from 'styled-components';
import Hero from '../Shared/Hero';
import { PageBodyContainer } from '../Shared/SharedStyles';
import { Section } from './Home';
import beavers from '../img/beavers.png';
import badges from '../img/Badge_Placement_Beavers.jpeg';

interface ILogoProps {
  top: number
}

export const PageLogo = styled.img`
    position: absolute;
    top: ${(p:ILogoProps) => `${p.top}%`};
    left: calc(50% - 400px);
    width: 800px;

    @media(max-width: 850px) {
      top: ${(p:ILogoProps) => `${p.top + 4}%`};
      width: 600px;
      left: calc(50% - 300px);
    }

    @media(max-width: 620px) {
      top: ${(p:ILogoProps) => `${p.top + 8}%`};
      width: 400px;
      left: calc(50% - 200px);
    }
    
    @media(max-width: 520px) {
      top: ${(p:ILogoProps) => `${p.top + 7}%`};
      width: 80%;
      left: 10%;
    }    
`;

export const BadgeLayout = styled.img`
  margin: auto;
  width: 100%;
  max-width: 800px;
`;

const Beavers: FC = () => {
    return (
      <PageBodyContainer>
        <Hero image="beavers" />
        <PageLogo src={beavers} top={68} />
        <Section>
          <div className="wrapper">

          </div>
        </Section>

        <Section id="uniform">
          <div className="wrapper">
            <BadgeLayout src={badges} />
        </div>
      </Section>
      </PageBodyContainer>
    );
}

export default Beavers;